body {
  overflow-x: hidden;
}


.header {
  margin-top: 10px;
  min-width: 100vw;
}

.headerTitles {
  display: flex;
  flex-direction: column;
  align-items: center;
  font-family: "Bebas Neue", cursive;
  color: #444;
}
.headerImage {
  width: 100%;
  height: 200px;

  object-fit: cover;
}

.posts {
  flex: 9 1;
  display: flex;
  flex-wrap: wrap;
  margin: 20px;
}
@media only screen and (max-width: 750px) {
  .posts {
    justify-content: center;
    order: 1;
  }
}

.post {
  width: 385px;
  margin: 0px 25px 40px 25px;
}

.postImg {
  width: 100%;
  height: 280px;
  object-fit: cover;
  border-radius: 7px;
}

.postInfo {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.postCat {
  font-family: "Bebas Neue", "sans-serif";
  font-size: 11px;
  color: #be9656;
  line-height: 20px;
  margin-top: 15px;
  margin-right: 10px;
  cursor: pointer;
}

.postTitle {
  font-family: "Bebas Neue", "sans-serif";
  font-size: 24px;
  font-weight: 300;
  margin-top: 15px;
  cursor: pointer;
}

.postDate {
  font-family: "Roboto", "sans-serif";
  font-style: italic;
  font-size: 13px;
  color: gray;
}

.postDesc {
  font-family: "Roboto", "sans-serif";
  font-size: 14px;
  color: #444;
  line-height: 24px;
  margin-top: 15px;
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 4;
  -webkit-box-orient: vertical;
  text-align: justify;
}

.sidebar {
  flex: 3 1;
  margin: 20px;
  padding-bottom: 30px;
  background-color: #fdfbfb;
  border-radius: 10px;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.titleContainer {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}
.sidebarItem {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
}
.websiteTitle {
  font-size: 45px;
  font-weight: 900;
  align-self: left;
  color: rgb(14, 205, 226);
}

.sidebarTitle {
  margin: 10px;
  padding: 5px;
  width: 80%;
  border-top: 1px solid #a7a4a4;
  border-bottom: 1px solid #a7a4a4;
  font-family: "Bebas Neue", "sans-serif";
  font-size: 20px;
  color: #222;
  font-weight: 600;
  line-height: 20px;
  text-align: center;
}

.sidebarImage {
  margin-top: 15px;
  width: 80%;
  height: 30vh;
  object-fit: cover;
}

.sidebarItem > p {
  padding: 30px;
  text-align: justify;
}

.sidebarList {
  list-style: none;
  margin-bottom: 30px;
  width: 100%;
}

.sidebarListItem {
  display: inline-block;
  width: 40%;
  cursor: pointer;
  text-align: center;
  margin-top: 15px;
  font-size: 20px;
}
.sidebarSocial {
  margin-top: 15px;
  width: 250px;
  display: flex;
  align-items: center;
  justify-content: center;
}
.sidebarIcon {
  font-size: 50px;
  margin-left: 10px;
  cursor: pointer;
}
.sidebarTalks {
  align-self: center;
  line-height: 2rem;
  text-align: center;
}

@media only screen and (max-width: 750px) {
}
.socialIconLinks {
  color: black;
}

.home {
  display: flex;
  justify-content: center;
  align-items: flex-start;
}

@media only screen and (max-width: 750px) {
  .home {
    flex-direction: column;
  }
}

.top {
  width: 100%;
  height: 50px;
  background-color: white;
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  display: flex;
  align-items: center;
  font-family: "Roboto", sans-serif;
  z-index: 999;
}

.topLeft,
.topRight {
  flex: 3 1;
  display: flex;
  align-items: center;
  justify-content: center;
}

.topIcon {
  font-size: 20px;
  margin-right: 10px;
  cursor: pointer;
  color: #444;
}

.topCenter {
  flex: 6 1;
}

.topImage {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  object-fit: cover;
  cursor: pointer;
}

.topList {
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 0;
  padding: 0;
  list-style: none;
}

.topListItem {
  margin-right: 20px;
  font-size: 18px;
  font-weight: 500;
  cursor: pointer;
}

.topSearchIcon {
  font-size: 18px;
  color: #666;
  cursor: pointer;
  margin-left: 15px;
}

@media only screen and (max-width: 580px) {
  .top {
    display: flex;
    flex-direction: column;
    height: 100px;
  }
}

.login {
  height: calc(100vh - 50px);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background: linear-gradient(
      rgba(255, 255, 255, 0.5),
      rgba(255, 255, 255, 0.5)
    ),
    url("https://www.incimages.com/uploaded_files/image/1920x1080/getty_623603280_367828.jpg");
  background-size: cover;
}
.loginTitle {
  font-size: 50px;
  margin-top: 20px;
  display: flex;
  flex-direction: column;
}
.loginForm {
  margin-top: 20px;
  width: 100%;
  max-width: 400px;
  display: flex;
  flex-direction: column;
}

.loginForm.loginFormLabel {
  margin: 50px 0;
}

.loginInput {
  padding: 10px;
  background-color: white;
  border: none;
}

.loginButton {
  margin-top: 20px;
  padding: 10px;
  font-size: 20px;
  cursor: pointer;
  background-color: lightcoral;
  border: none;
  color: white;
  border-radius: 10px;
}
.loginRegisterButton {
  position: absolute;
  top: 60px;
  right: 20px;
  background-color: teal;
  cursor: pointer;
  border: none;
  padding: 15px;
  color: white;
  border-radius: 10px;
  font-size: 16px;
}

.register {
  height: calc(100vh - 50px);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background: linear-gradient(
      rgba(255, 255, 255, 0.5),
      rgba(255, 255, 255, 0.5)
    ),
    url("https://api.time.com/wp-content/uploads/2018/09/single-woman-benefits.jpg?quality=85&w=1200&h=628&crop=1");
  background-size: cover;
}
.registerTitle {
  font-size: 50px;
  margin-top: 20px;
  display: flex;
  flex-direction: column;
}
.registerForm {
  margin-top: 20px;
  width: 100%;
  max-width: 400px;
  display: flex;
  flex-direction: column;
}

.registerForm.registerFormLabel {
  margin: 50px 0;
}

.registerInput {
  padding: 10px;
  background-color: white;
  border: none;
}

.registerButton {
  margin-top: 20px;
  padding: 10px;
  font-size: 20px;
  cursor: pointer;
  background-color: teal;
  border: none;
  color: white;
  border-radius: 10px;
}
.registerLoginButton {
  position: absolute;
  top: 60px;
  right: 20px;
  background-color: lightcoral;
  cursor: pointer;
  border: none;
  padding: 15px;
  color: white;
  border-radius: 10px;
  font-size: 16px;
}
.somethingwentwrong {
  text-align: center;
  font-size: 24px;
  font-weight: bold;
  color: red;
  margin-top: 15px;
}

.settings {
  display: flex;
}
.settingsWrapper {
  flex: 9 1;
  padding: 20px;
}

.settingsTitle {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.settingsUpdateTitle {
  font-size: 30px;
  margin-bottom: 20px;
  color: lightcoral;
}
.settingsDeleteTitle {
  color: red;
  font-size: 12px;
  cursor: pointer;
}
.settingsForm {
  display: flex;
  flex-direction: column;
}
.settingsProfilePicture {
  display: flex;
  align-items: center;
  margin: 10px 0;
}

.settingsProfilePicture > img {
  width: 70px;
  height: 70px;
  border-radius: 20px;
  object-fit: cover;
}
.settingsPPIcon {
  width: 25px;
  height: 25px;
  border-radius: 50%;
  background-color: lightcoral;
  color: white;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-left: 10px;
  cursor: pointer;
}

.settingsForm > label {
  font-size: 20px;
  margin-top: 20px;
}
.settingsForm > input {
  color: gray;
  margin: 10px 0;
  height: 30px;
  border: none;
  border-bottom: 1px solid lightgray;
}
.settingsSubmit {
  width: 150px;
  align-self: center;
  border: none;
  border-radius: 10px;
  color: white;
  background-color: teal;
  padding: 10px;
  margin-top: 20px;
  cursor: pointer;
}

.singlePost {
  flex: 9 1;
}

.singlePostWrapper {
  padding: 20px;
  padding-right: 0;
  display: flex;
  flex-direction: column;
}

.singlePostImg {
  width: 100%;
  height: 300px;
  border-radius: 5px;
  object-fit: cover;
}

.singlePostTitle {
  text-align: center;
  margin: 10px;
  font-family: "Roboto", "sans-serif";
  font-size: 28px;
}

.singlePostEdit {
  float: right;
  font-size: 16px;
}

.singlePostIcon {
  margin-left: 10px;
  cursor: pointer;
}

.singlePostIcon:first-child {
  color: teal;
}

.singlePostIcon:last-child {
  color: tomato;
}
.singlePostInfo {
  margin-bottom: 20px;
  display: flex;
  justify-content: space-between;
  font-size: 16px;
  color: #b39656;
  font-weight: 200;
}

.singlePostDesc {
  color: #666;
  font-size: 18px;
  line-height: 25px;
}

.singlePostDesc::first-letter {
  margin-left: 20px;
  font-size: 30px;
  font-weight: 600;
}

.singlePostTitleInput {
  margin: 10px;
  font-family: "Roboto", "sans-serif";
  font-size: 28px;
  text-align: center;
  border: none;
  color: #666;
}
.singlePostTitleInput:focus {
  outline: none;
  border-bottom: 1px solid lightgray;
}
.singlePostDescInput {
  border: none;
  color: #666;
  font-size: 18px;
  line-height: 25px;
}
.singlePostDescInput:focus {
  outline: none;
}

.singlePostButton {
  width: 100px;
  border: none;
  background-color: teal;
  padding: 15px;
  color: white;
  border-radius: 5px;
  cursor: pointer;
  align-self: flex-end;
  margin-top: 20px;
}

.single {
  display: flex;
}

.write {
  padding-top: 50px;
}
.writeImg {
  margin-left: 150px;
  width: 70vw;
  height: 250px;
  border-radius: 10px;
  object-fit: cover;
}

.writeForm {
  position: relative;
}
.writeFormGroup {
  margin-left: 150px;
  display: flex;
  align-items: center;
}
.writeIcon {
  width: 25px;
  height: 25px;
  border-radius: 50%;
  border: 1px solid;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 20px;
  color: gray;
  cursor: pointer;
}

.writeInput {
  font-size: 20px;
  border: none;
  padding: 20px;
  width: 70vw;
}
.writeInput:focus {
  outline: none;
}
.writeText {
  font-size: 20px;
  height: 100vh;
}

.writeSubmit {
  position: absolute;
  top: 20px;
  right: 50px;
  color: white;
  background-color: teal;
  padding: 10px;
  border: none;
  border-radius: 10px;
  cursor: pointer;
  font-size: 16px;
}

@media only screen and (max-width: 750px) {
  .writeSubmit {
    position: relative;
    margin-left: auto;
  }
  .writeForm {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: flex-start;
  }
  .writeText {
    height: auto;
  }
}

.mainContainer {
  display: flex;
  height: 100vh;
  justify-content: center;
  align-items: center;
}
.textContainer {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-start;
  height: 700px;
  width: 500px;
  font-size: 20px;
  line-height: 40px;
  text-align: left;
  padding: 20px;
}
.MyPIContainer {
  height: 700px;
  width: 500px;
  object-fit: cover;
}
.PIImage {
  width: 100%;
  height: 100%;
}

